import React, { Component } from 'react';
import './App.css';
import './Slider2.css';
//import Map from './components/Map';
import {Map, TileLayer, ImageOverlay, Popup} from 'react-leaflet';
import Control from 'react-leaflet-control';
import Menu from './components/Menu';
import Sensors from './components/Sensors';
import axios from 'axios';
//import { Popup } from 'leaflet';
 
class App extends Component {
  state = {
    radars: null,
    imageOpacity: 0.6,
    currentSensorId: "null",
    currentImageID: 0,
    interval: null,
    popup: null
  }

  componentDidMount(){
    axios.get('./radars.json').then(res => {
      console.log(res.data);
      this.setState({
        radars: res.data,
        currentSensorId: "LEG",
        imageOpacity: 0.6,
        currentImageID: res.data.filter((rad) => rad.id === "LEG")[0].images.length - 1,
        interval: null
      })
    });
  }

  function = () => {
    
  }

  changeSensor = (radar) => {
    console.log(radar);
    var id = this.state.currentImageID;
    this.setState({
      currentSensorId: "null",
      currentImageID: 0
    });
    
    this.setState({
      currentSensorId: radar.id,
      currentImageID: id
    });
    //document.getElementById("sliderange").value = 3;
  }

  changeOpacity = (e) => {
    console.log(e);
    this.setState(
      {
        imageOpacity: e
      }
    );
  }

  getSensorByID = (id) => {
    return this.state.radars.filter( (radar) => radar.id === id )[0]
  }

  getCurrentSensor = () => {
    return this.state.radars.filter( (radar) => radar.id === this.state.currentSensorId )[0]
  }

  incrementImage = () => {
    this.setState({
      currentImageID: (this.state.currentImageID+1)%12
    });
  }

  animation = () => {
    if(!this.state.interval){
      console.log("animation on");
      document.getElementById("animBtn").className = "animationButton pause";
      this.setState({
        interval: setInterval(this.incrementImage,250)
      });
    }
    else{
      console.log("animation off");
      document.getElementById("animBtn").className = "animationButton play";
      this.setState({
        interval: clearInterval(this.state.interval)
      });
    }

  }

  changeImage = (e) => {
    console.log(e.target.value);
    if(this.state.interval){
      this.setState({
        interval: clearInterval(this.state.interval)
      });
    }
    this.setState({
      currentImageID: parseInt(e.target.value) - 1
    });
  }

  render(){
    if(this.state.radars){
      const {popup} = this.state;
      return (
      <Map center={[52.0,19.0]} zoom={8} zoomControl={false} id="map">
        <TileLayer url={'https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png'}
        minZoom={2}
        maxZoom={18} />
        <ImageOverlay url={this.getCurrentSensor().images[this.state.currentImageID].path}
        bounds={this.getCurrentSensor().boundingBox}
          opacity={this.state.imageOpacity}/>
        <Control position='topright' className="desktopControl">
          <Menu sensor={this.getCurrentSensor()} imgID={this.state.currentImageID} changeOpacity={this.changeOpacity} animate={this.animation} type="desktop"/>
        </Control>
        <Control position='bottomleft' className='animationControl'>
          <input type='range' id="sliderange" className='animationSlider' min={1} max={12} value={this.state.currentImageID + 1} onInput={this.changeImage}/>
        </Control>
        <Sensors list={this.state.radars} changeSensor={this.changeSensor}/>
        {
          popup && 
          <Popup key="popup-0" position={[52.0,19.0]} className="popup">
            <button>ALYX</button>
          </Popup>
        }
      </Map>
    );
    } else {
      return(null);
    }
  }
}

export default App;
