import React, { Component } from 'react'

export class Slider extends Component {
    state = {
        val: this.props.default
    }
    onChange = (e) => {
        this.setState({
            val: e.target.value
        });
        var decVal = parseInt(e.target.value)/100;
        this.props.handler(decVal);
    }
    render() {
        return (
            <React.Fragment>
                <p>{this.props.name + '  ' + this.state.val + "%"}</p>
                <input 
                type="range" 
                min={this.props.min} 
                max={this.props.max} 
                className="opacitySlider" 
                defaultValue={this.props.default}
                onChange={this.onChange}/>
            </React.Fragment>
        )
    }
}

export default Slider
