import React, { Component } from 'react'
import { DomEvent } from 'leaflet';
import Slider from './Slider'

export class Menu extends Component {
    constructor(props){
        super(props);
        this.container = null;
    }

    refContainer = (element) => {
        this.container = element;
        if(element){
            DomEvent.disableScrollPropagation(element);
        }
    }

    changeOpacity = (e) => {
        //console.log(e);
        this.props.changeOpacity(e);
    }

    render() {
        if(this.props.type === "desktop"){
            return (
                <div className="menu" ref={this.refContainer}>
                    <div className="productHeader">
                        <h1>{this.props.sensor.name}</h1>
                        <h1>{`PPI(dBZ) 0.5°`}</h1>
                        <h1>{this.props.sensor.images[this.props.imgID].date}</h1>
                    </div>
                    <div className="productHeaderMobile">
                        <h1>{this.props.sensor.name}</h1>
                        <h1>{"PPI(dBZ) 0.5° " + this.props.sensor.images[this.props.imgID].date}</h1>
                    </div>
                    <div className="animationToolkit">
                        <button id="animBtn" className="animationButton play" onClick={this.props.animate}></button>
                    </div>
                    <div className="scaleWrapper">
                        <img src="./test_scale.svg" className="scale"></img>
                    </div>
                    <div className="metadata">
                        <div className="left-metadata">
                            <p className="left-metadata-entry">Skan:</p>
                            <p className="left-metadata-entry">Zasięg:</p>
                            <p className="left-metadata-entry">Rozdzielczość:</p>
                        </div>
                        <div className="right-metadata">
                            <p className="right-metadata-entry">doppler</p>
                            <p className="right-metadata-entry">125km</p>
                            <p className="right-metadata-entry">0.5km/px</p>
                        </div>
                    </div>
                    <div className="settings">
                        <Slider name="Przezroczystość:" min="0" max="100" default="60" handler={this.changeOpacity}/>
                        <div className="toggleWrapper">
                            <div className="ipolSwitch">
                                <label className="switch">
                                    <input type="checkbox"/>
                                    <span className="slider round"></span>
                                </label>
                            </div>
                            <div className="ipolLegend">
                                <p>Interpolacja</p>
                            </div>
                        </div>
                        <p>Prędkość animacji:</p>
                        <input type="range" min="0" max="100" defaultValue="60" className="opacitySlider"/>
                    </div>
                </div>
            )
        } else {
            console.log("dupa xd")
            return(
                <div className="mobileMenu">
                    <div className="productHeader">
                        <h1>{this.props.sensor.name + " PPI(dBZ) 0.5°"}</h1>
                        <h1>{this.props.sensor.images[this.props.imgID].date}</h1>
                    </div>
                </div>
            )
        }
    }
}

export default Menu;